import appTimeScreening from "../../../../components/page-component/app-time-screening/app-time-screening.vue";



import { mapState } from "vuex";
export default {
    data() {
        return {
            theme: {
                color: '#ff4544'
            },
            mch_id: 0,
            cancelRefund: false,
            time_start: [],
            noAddress: false,
            status: '1',
            _num: '0',
            more_list: false,
            addressId: '0',
            refund_price: 0,
            page: 1,
            notRefund: false,
            openAddress: false,
            isReason: false,
            menu: [
                {
                    name: '待收货',
                    value: '3'
                },
                {
                    name: '待处理',
                    value: '7'
                },
                {
                    name: '已取消',
                    value: '6'
                },
                {
                    name: '已完成',
                    value: '8'
                }
            ],
            active: null,
            show: false,
            start: [],
            end: [],
            search: false,
            keyword: '',
            list: [],
            candidate: [],
            date_start: '',
            date_end: '',
            time: 0,
            inSearch: false,
            address: [],
            chooseTime: false,
            isRefund: false,
            noRefund: false,
            beToSend: false,
            cancelOrder: false,
            confirmOrder: false,
            detail: {},
            changePrice: false,
            callPhone: false,
            custom: false,
            mobile: '',
            isSend: false,
            sendType: 0,
            price: 0,
            express: 0,
            msg: false,
            first: false,
            searchResult: false,
            total: 0,
            about: '',
            actions: [{
                    name: '取消',
                    color: '#666666',
                },
                {
                    name: '去设置',
                    color: '#ff4544',
                    loading: false
                }
            ]
        }
    },
    components: {



        "app-time-screening": appTimeScreening
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.info,
            adminImg: state => state.mallConfig.__wxapp_img.mch,
        })
    },
    methods: {
        toDetail(item,status) {
            let id = item.order_id;
            if(status == 2) {
                id = item.id
            }
            uni.navigateTo({
                url: '/plugins/mch/mch/order-detail/order-detail?id='+id+'&status='+status+'&mch_id='+this.mch_id
            })
        },
        toRedirect(url) {
            uni.redirectTo({
                url: url
            })
        },
        // 取消
        cancel() {
            this.about = '';
            this.isRefund = false;
            this.chooseTime = false;
            this.noRefund = false;
            this.changePrice = false;
            this.cancelOrder = false;
            this.confirmOrder = false;
            this.beToSend = false;
            this.callPhone = false;
            this.isSend = false;
            this.notRefund = false;
            this.openAddress = false;
            this.isReason = false;
            this.addressId = 0;
            this.noAddress = false;
        },
        toChangeAddress(item) {
            this.beToSend = false;
            let order_no = item.order_no ? item.order_no : this.detail.order_no
            uni.navigateTo({
                url: '/plugins/mch/mch/change-add/change-add?mch_id='+this.mch_id+'&order_no='+order_no
            })
        },
        // 切换类别
        tabStatus(e) {
            let that = this;
            that.status = e;
            that.active = null;
            that.list = [];
            that.date_start = '';
            that.date_end = '';
            that.keyword = '';
            that.time = 0;
            that.show = false;
            that._num = '0';
            if(e == 2) {
                that._num = 0
            }
            that.page = 1;
            this.getList();
        },
        toCall(e) {
            if(this.status == 1) {
                this.mobile = e.mobile;
            }else {
                this.mobile = e.order.mobile;
            }
            this.callPhone = !this.callPhone;
        },
        beConfirm() {
            let that = this;
            that.$request({
                url: that.$api.app_admin.shou_huo,
                data: {
                    refund_order_id: that.detail.id
                },
                method: 'post'
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    uni.showToast({
                        title: response.msg,
                        duration: 1000,
                        type: 'success',
                        mask: false
                    });
                    that.cancel();
                    setTimeout(v=>{
                        that.list = [];
                        that.page = 1;
                        uni.showLoading({
                            title: '加载中...'
                        });
                        that.getList();
                    },1000)
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        call() {
            uni.makePhoneCall({
                phoneNumber: this.mobile
            })
            this.callPhone = !this.callPhone;
        },
        toSearch() {
            this.search = true;
            this.list = [];
            this.searchResult = false;
            this.candidate = this.$storage.getStorageSync('mch_keyword') ? this.$storage.getStorageSync('mch_keyword') : [];
            this.inSearch = false;
        },
        keywordSearch(e) {
            this.keyword = e;
            this.searchResult = true;
            this.getList();
        },
        // 清除搜索记录
        clear() {
            let that = this;
            this.$storage.removeStorageSync('mch_keyword');
            that.candidate = [];
            uni.showToast({
                title: '清理成功',
                duration: 1000,
                type: 'success',
                mask: false
            });
        },
        // 搜索
        searchMethod() {
            let value = this.$storage.getStorageSync('mch_keyword') ? this.$storage.getStorageSync('mch_keyword') : [];
            if (this.keyword.length == 0) {
                return
            } else if (value.length > 0) {
                value.unshift(this.keyword)
            } else {
                value = [this.keyword]
            }
            this.page = 1;
            this.list = [];
            this.getList();
            value.forEach(function(row, index) {
                if (value[0] == value[index] && index > 0) {
                    value.splice(index, 1)
                }
            })
            this.$storage.setStorageSync('mch_keyword', value);
            this.inSearch = true;
            this.searchResult = true;
        },
        // 取消搜索
        cancelSeacrch() {
            this.search = false;
            this.keyword = '';
            this.list = [];
            this.page = 1;
            this.getList();
        },
        // 
        // 确认选择时间
        toChoose(res) {
            let that = this;
            uni.showLoading({
                title: '加载中...'
            });
            that.date_start = res.date_start;
            that.date_end = res.date_end;
            that.time = res.choose;
            that.page = 1;
            that.list = [];
            that.getList();
            that.chooseTime = false;
        },
        // 打开时间筛选
        toTime() {
            let that = this;
            that.chooseTime = !that.chooseTime;
            that.show = false;
        },
        // 选择更多状态
        chooseItem(e) {
            let that = this;
            that._num = e;
            that.menu.forEach(function(row, index) {
                if (row.value == that._num) {
                    that.active = row;
                }
            })
            that.show = !that.show;
            that.page = 1;
            that.list = [];
            that.getList();
        },
        // 切换状态
        tab(e) {
            this._num = e;
            this.show = false;
            this.active = null;
            this.list = [];
            this.page = 1;
            this.getList();
        },
        getList() {
            let that = this;
            that.about = '';
            let url;
            let refund_status = 0;
            if (that.status == 2) {
                refund_status = that._num;
            }
            uni.showLoading({
                title: '加载中...'
            });
            that.$request({
                url: that.$api.mch.order_list,
                data: {
                    status: that.status == 2 ? '0': that._num,
                    mch_id: that.mch_id,
                    end_date: that.date_end,
                    start_date: that.date_start,
                    order_type: that.status == 2 ? 'refund_order':'order',
                    refund_status: refund_status,
                    page: that.page,
                    keyword: that.keyword
                }
            }).then(response=>{
                that.$hideLoading();
                uni.hideLoading();
                that.first = true;
                if(response.code == 0) {
                    let list = response.data.list;
                    if (that.status == 2) {
                        let address = response.data.address;
                        address.forEach(function(row, index) {
                            row.address = row.address.replace(/"/g, '')
                            row.address = row.address.replace(/,/g, '')
                            row.address = row.address.replace('[', '')
                            row.address = row.address.replace(']', '')
                        })
                        that.address = address;
                    } else {
                        list.forEach(function(row) {
                            row.order_id = row.id;
                            row.detail.forEach(function(res) {
                                if (res.refund_status > 0) {
                                    row.have_refund = 1;
                                }
                            })
                        })
                    }
                    that.more_list = false;
                    if (list.length == response.data.pagination.pageSize) {
                        that.more_list = true;
                    }
                    that.page++;
                    that.list = that.list.concat(list);
                    that.$forceUpdate();
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
                uni.hideLoading();
            });
        },
        toCancelorder(e) {
            this.detail = e;
            this.cancelOrder = !this.cancelOrder;
        },
        toConfirm(e) {
            this.detail = e;
            this.confirmOrder = !this.confirmOrder;
        },
        cancelSubmit(type) {
            let that = this;
            uni.showLoading({
                title: '加载中...'
            });
            let url = type == 1 ? that.$api.mch.cancel : that.$api.mch.force_cancel
            that.$request({
                url: url,
                data: {
                    status: 1,
                    mch_id: that.mch_id,
                    remark: '',
                    order_id: that.detail.id
                },
                method: 'post'
            }).then(response => {
                uni.hideLoading();
                if (response.code == 0) {
                    uni.showToast({
                        title: '取消成功',
                        duration: 2000,
                        type: 'success',
                        mask: false
                    });
                    that.isRefund = false;
                    that.cancelOrder = false;
                    setTimeout(()=>{
                        that.list = [];
                        that.page = 1;
                        that.getList();
                    },1000)
                } else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
                uni.showToast({
                    title: response,
                    icon: 'none',
                    duration: 1000
                });
            });
        },
        toChange(e) {
            this.detail = e;
            this.changePrice = !this.changePrice;
            this.price = e.total_goods_price;
            this.express = e.express_price;
            this.total = '￥' + e.total_pay_price;
        },
        priceInput: function(e) {
            if (e.detail.value > -0.01) {
                this.total = '￥' + (+e.detail.value + +this.express).toFixed(2)
            } else {
                this.total = '数据有误'
            }
        },
        expressInput: function(e) {
            if (e.detail.value > -0.01) {
                this.total = '￥' + (+e.detail.value + +this.price).toFixed(2)
            } else {
                this.total = '数据有误'
            }
        },
        // 确认修改价格
        submitChange() {
            let that = this;
            uni.showLoading({
                title: '加载中...'
            });
            if (that.price > -0.01 && that.express > -0.01) {
                that.$request({
                    url: that.$api.mch.update_total_price,
                    data: {
                        order_id: that.detail.id,
                        mch_id: that.mch_id,
                        total_price: that.price,
                        express_price: that.express
                    },
                    method: 'post'
                }).then(response => {
                    uni.hideLoading();
                    if (response.code == 0) {
                        uni.showToast({
                            title: response.msg,
                            duration: 2000,
                            type: 'success',
                            mask: false
                        });
                        that.page = 1;
                        that.list = [];
                        that.changePrice = false;
                        that.getList();
                    } else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    uni.hideLoading();
                    uni.showToast({
                        title: response,
                        icon: 'none',
                        duration: 1000
                    });
                });
            } else {
                if (!that.price || typeof(that.price) != 'number') {
                    uni.showToast({
                        title: '商品总价必须大于等于0',
                        icon: 'none',
                        duration: 1000
                    });
                } else if (!that.express || typeof(that.express) != 'number') {
                    uni.showToast({
                        title: '运费必须大于等于0',
                        icon: 'none',
                        duration: 1000
                    });
                }
            }
        },
        toRefundOrder(e) {
            let that = this;
            
            that.list = [];
            that.more_list = false;
            that.keyword = e;
            that.status = 2;
            that._num = 0;
            that.search = true;
            that.searchMethod();
        },
        toSend(e) {
            this.detail = e;
            if(e.send_type == 1 && !e.address) {
                this.beToSend = true;
            }else {
                this.isSend = true;
            }
        },
        toExpress(e,is_send) {
            let id = e.id;
            let order_refund_id = e.refund;
            if (id > 0) {
                uni.navigateTo({
                    url: '/plugins/mch/mch/send/send?id=' + id + '&is_send=' + is_send + '&mch_id=' + this.mch_id
                })
            } else if (order_refund_id) {
                uni.navigateTo({
                    url: '/plugins/mch/mch/send/send?order_refund_id=' + order_refund_id + '&is_send=' + is_send + '&mch_id=' + this.mch_id
                })
            }
        },
        toSendType() {
            let that = this;
            if (that.sendType == 1) {
                if (that.detail.status_text) {
                    uni.showLoading({
                        title: '加载中...'
                    });
                    that.$request({
                        url: that.$api.mch.refund_handle,
                        data: {
                            is_express: 0,
                            merchant_remark: '',
                            mch_id: that.mch_id,
                            type: that.detail.type,
                            is_agree: 1,
                            order_refund_id: that.detail.id
                        },
                        method: 'post'
                    }).then(response => {
                        uni.hideLoading();
                        if (response.code == 0) {
                            uni.showToast({
                                title: response.msg,
                                type: 'success',
                                mask: false,
                                duration: 2000
                            });
                            that.list = [];
                            that.page = 1;
                            that.isSend = false;
                            that.sendType = 0;
                            that.getList();
                        } else {
                            uni.showToast({
                                title: response.msg,
                                icon: 'none',
                                duration: 1000
                            });
                        }
                    }).catch(response => {
                        uni.hideLoading();
                        uni.showToast({
                            title: response,
                            icon: 'none',
                            duration: 1000
                        });
                    });
                } else {
                    uni.showLoading({
                        title: '加载中...'
                    });
                    let para = {
                        is_express: 2,
                        mch_id: that.mch_id,
                        words: '',
                        order_id: that.detail.id
                    }
                    if(that.detail.detailExpress.length > 0) {
                        para.express_id = that.detail.detailExpress[0].id
                    }
                    that.$request({
                        url: that.$api.mch.order_send,
                        data: para,
                        method: 'post'
                    }).then(response => {
                        uni.hideLoading();
                        if (response.code == 0) {
                            uni.showToast({
                                title: response.msg,
                                type: 'success',
                                mask: false,
                                duration: 2000
                            });
                            that.isSend = false;
                            setTimeout(()=>{
                                that.list = [];
                                that.page = 1;
                                that.sendType = 0;
                                that.getList();
                            },1000)
                        } else {
                            uni.showToast({
                                title: response.msg,
                                icon: 'none',
                                duration: 1000
                            });
                        }
                    }).catch(response => {
                        uni.hideLoading();
                        uni.showToast({
                            title: response,
                            icon: 'none',
                            duration: 1000
                        });
                    });
                }
            } else {
                that.isSend = false;
                if (that.detail.status_text) {
                    uni.navigateTo({
                        url: '/plugins/mch/mch/send/send?order_refund_id=' + that.detail.id + '&mch_id=' + that.mch_id + '&is_send=' + that.detail.is_send
                    })
                } else {
                    uni.navigateTo({
                        url: '/plugins/mch/mch/send/send?id=' + that.detail.id + '&mch_id=' + that.mch_id + '&is_send=' + that.detail.is_send
                    })
                }
            }
        },
        look(e) {
            uni.previewImage({
                current: e, // 当前显示图片的http链接
                urls: [e] // 需要预览的图片http链接列表
            })
        },
        lookAbout(e) {
            this.detail = e;
            this.isReason = true;
        },
        toRefund(e) {
            this.detail = e;
            this.refund_price = e.refund_price;
            this.isRefund = !this.isRefund;
        },
        agree() {
            let that = this;
            uni.showLoading({
                title: '处理中...'
            });
            if (that.detail.refund_price > 0) {
                that.$request({
                    url: that.$api.mch.refund_handle,
                    data: {
                        order_refund_id: that.detail.id,
                        type: that.detail.type,
                        is_agree: 1,
                        mch_id: that.mch_id,
                        refund_price: that.refund_price,
                        merchant_remark: that.about
                    },
                    method: 'post'
                }).then(response => {
                    uni.hideLoading();
                    if (response.code == 0) {
                        uni.showModal({
                            title: '提示',
                            content: response.msg,
                            showCancel: false,
                            success: function (res) {
                                if (res.confirm) {
                                    that.list = [];
                                    that.notRefund = false;
                                    that.openAddress = false;
                                    that.isRefund = false;
                                    that.addressId = 0;
                                    that.page = 1;
                                    that.getList();
                                }
                            }
                        });
                    } else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    uni.hideLoading();
                    uni.showToast({
                        title: response,
                        icon: 'none',
                        duration: 1000
                    });
                });
            } else {
                uni.showToast({
                    title: '退款金额需大于零',
                    icon: 'none',
                    duration: 1000
                });
            }
        },
        refundHandle(type,detail,cancel) {
            if (cancel == 1) {
                this.cancelRefund = true;
            }
            if (type == 1) {
                if (this.address.length == 0) {
                    this.noAddress = true;
                } else {
                    this.detail = detail;
                    this.openAddress = true;
                }
            } else if (type == 2) {
                this.detail = detail;
                this.notRefund = true;
            }
            this.$forceUpdate();
        },

        toAgreeCancel(e) {
            this.detail = e;
            this.isRefund = !this.isRefund;
        },
        decline(type) {
            let that = this;
            if (type == 1) {
                if (that.addressId < 1) {
                    uni.showToast({
                        title: '请选择地址',
                        icon: 'none',
                        duration: 1000
                    });
                    return false;
                }
            }
            if (that.cancelRefund) {
                type = 2;
            }
            uni.showLoading({
                title: '处理中...'
            });
            that.$request({
                url: that.$api.mch.refund_handle,
                data: {
                    order_refund_id: that.detail.id,
                    type: that.detail.type,
                    is_agree: type,
                    mch_id: that.mch_id,
                    address_id: that.addressId,
                    refund_price: that.detail.refund_price,
                    merchant_remark: that.about
                },
                method: 'post'
            }).then(response => {
                uni.hideLoading();
                if (response.code == 0) {
                    uni.showModal({
                        title: '提示',
                        content: response.msg,
                        showCancel: false,
                        success: function (res) {
                            if (res.confirm) {
                                that.page = 1;
                                that.list = [];
                                that.notRefund = false;
                                that.cancelOrder = false;
                                that.openAddress = false;
                                that.addressId = 0;
                                that.getList();
                            }
                        }
                    });
                } else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
                uni.showToast({
                    title: response,
                    icon: 'none',
                    duration: 1000
                });
            });
        },
        chooseAddress(e) {
            if (this.addressId == e) {
                this.addressId = ''
            }else {
                this.addressId = e
            }
        },
        noCancel() {
            let that = this;
            uni.showLoading({
                title: '处理中...'
            });
            that.$request({
                url: that.$api.mch.cancel,
                data: {
                    status: 2,
                    remark: that.about,
                    mch_id: that.mch_id,
                    order_id: that.detail.id
                },
                method: 'post'
            }).then(response => {
                uni.hideLoading();
                if (response.code == 0) {
                    uni.showToast({
                        title: response.msg,
                        type: 'success',
                        mask: false,
                        duration: 2000
                    });
                    that.list = [];
                    that.page = 1;
                    that.noRefund = false;
                    that.getList();
                } else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
                uni.showToast({
                    title: response,
                    icon: 'none',
                    duration: 1000
                });
            });
        },
        beNotRefund(e) {
            this.detail = e;
            this.noRefund = !this.noRefund;
        },
    },
    onReachBottom: function() {
        if (this.more_list) {
            this.getList();
        }
    },
    onShow: function() {
        if (!this.search && this.first) {
            this.list = [];
            this.page = 1;
            this.getList();
        }
    },
    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.status = '1';
        that._num = '0';
        that.mch_id = options.mch_id;
        that.getList();
    }
}
